<script>
import { createEventDispatcher } from 'svelte';

export let recipients = [];
export let message = '';
export let possibleRecipients;

let showQuickReplies = !message;
const dispatch = createEventDispatcher();

let stdMessages = {
	'Bekreft data mottatt': 'Hei,\ntakk for at du har sendt inn skjema om loppehenting! :)\n\nVi henter hver kveld mellom 24. og 26. august. Vi kontakter deg pÃ¥ dette nummeret fÃ¸r henting.\n\nVennlig hilsen Ila og BoltelÃ¸kka skolekorps',
	'Hentes snart': 'Hei,\ntakk for at du vil gi korpset lopper. Passer det om noen kommer og henter hos deg snart?\n\nVennlig hilsen Ila og BoltelÃ¸kka skolekorps',
	'Ikke IKEA': 'Hei,\ntakk for at du vil gi korpset lopper! Dessverre har vi dÃ¥rlig erfaring med Ã¥ selge IKEA-mÃ¸bler, sÃ¥ slike vil vi helst ikke ta imot.\n\nVennlig hilsen Ila og BoltelÃ¸kka skolekorps',
	'Ikke sofa': 'Hei,\ntakk for at du vil gi korpset lopper! Dessverre har vi dÃ¥rlig erfaring med Ã¥ selge sofaer pÃ¥ loppemarked. Vi vil helst ikke ta imot sofaer med mindre de er av spesielt god kvalitet.\n\nVennlig hilsen Ila og BoltelÃ¸kka skolekorps',
	'Rekker ikke': 'Hei,\ntakk for at du vil gi korpset lopper! Dessverre rekker vi ikke Ã¥ hente loppene dine i kveld. Dersom du har mulighet til Ã¥ levere i skolegÃ¥rden, er det supert.\n\nVennlig hilsen Ila og BoltelÃ¸kka skolekorps',
	'For langt borte': 'Hei,\ntakk for at du vil gi korpset lopper! Dessverre rekker vi ikke Ã¥ hente tingene dine, fordi det er litt for langt borte fra vÃ¥rt omrÃ¥de. Vi foreslÃ¥r at du  tar kontakt med et lokalt korps og spÃ¸r om de skal hente lopper snart.\n\nVennlig hilsen Ila og BoltelÃ¸kka skolekorps',
	'Send foto?': 'Hei,\ntakk for at du vil gi korpset lopper! Kan du sende meg et foto av loppene?\n\nVennlig hilsen Ila og BoltelÃ¸kka skolekorps',
};

function send() {
	if (typeof recipients === 'string') {
		dispatch('sms', {
			recipients: recipients.split(/,\s*/g),
			message,
		});
	} else {
		dispatch('sms', {
			recipients,
			message,
		});		
	}
}

function addMessage(name) {
	if (name && stdMessages[name]) {
		message = stdMessages[name];
	}
}

</script>
<style type="text/css">
	form {
		display: table;
		width: 90%;
		margin-left: 5%
	}
	p {
		display: table-row;
		width: 100%;
		margin-top: 8px;
	}
	span {
		display: table-cell;
		vertical-align: top;
		padding-top: 8px;
	}
	span:first-child {
		width: 30%;
	}
	span * {
		width: 100%;
	}
	span button {
		width: 40%;
	}
	span button:nth-child(2) {
		margin-left: 8px;
	}
	textarea {height: 100px;}
	input, textarea, select {font-size: 1em;}
	.to textarea { width: 90%; height: 3em; }
	.sms textarea { width: 90%; height: 200px; }
	select, input {font-size: 1em;}
</style>

<form on:submit|preventDefault={e => send()}>
	<p>
		<b>Til</b><span class="to">
			{#if possibleRecipients && possibleRecipients.length}
				<select multiple bind:value={recipients} required>
					{#each possibleRecipients as recipient}
						<option value={recipient.number}>
							{recipient.name}{#if recipient.address}  - {recipient.address}{/if}
						</option>
					{/each}
				</select>
			{:else}
				<textarea bind:value={recipients} pattern="[0-9 ,]" required></textarea>
			{/if}
		</span>
	</p>
	<p>
		<b>SMS</b>
		<span class="sms">
			<textarea bind:value={message} required></textarea>
		</span>
	</p>

	{#if showQuickReplies}
	<p>
		<b>Kjappe svar</b>
		<span>
			<select on:change={e => addMessage(e.target.value)}>
				<option></option>
				{#each Object.keys(stdMessages) as name}
					<option>{name}</option>
					{/each}
			</select>
		</span>
	</p>
	{/if}

	<p><span></span>
		<span>
			<button type="submit" class="p8 br2">Send</button> 
			<button on:click={e => dispatch('cancel')} class="p8 br2" type="button">Avbryt</button>
		</span>
	</p>
</form>